@use '../common/layout';
@use '../modules/honeycomb';

/////////////////////////////////////////////////////////////

#team {
  // Width:
  $unit: honeycomb.get-unit(5, "side-content");
  @include honeycomb.create(5, 3, $unit);
}

$team-pics-placement:
// pic  half-column  row  x-position
1  2  1  25%,
2  3  2  75%,
3  2  3  65%,
4  4  3,
5  1  4;

@include honeycomb.place-set($team-pics-placement, "team");

#hopes-fears {
  @include honeycomb.create(4, 2, 7rem);

  .caption {
    grid-column: 5/9;
    grid-row: 8/10;
    align-self: end;
  }
}

#hopes {
  @include honeycomb.place-image(1, 1);
  img {
    object-position: 0% 50%;
  }
}

#fears {
  @include honeycomb.place-image(2, 2);
  img {
    object-position: 100% 50%;
  }
}

/////////////////////////////////////////////////////////////

.build-or-achieve-by-2030 {
  aspect-ratio: 1 / 1;
  align-self: center;

  img {
    height: 100%;
    @extend %become-hexagon;
    object-fit: cover;
    object-position: 15% 50%;
  }
}

/////////////////////////////////////////////////////////////

#world-around-us {
  @include honeycomb.create(5, 4, 6rem);

  .caption {
    grid-column: 6/10;
    grid-row: 11/14;
    align-self: center;
  }
}

$world-around-us-pics-placement:
// pic  half-column  row
1  2  1,
2  1  2,
3  2  3,
4  4  3,
5  1  4;

@include honeycomb.place-set($world-around-us-pics-placement, "world-around-us");


/////////////////////////////////////////////////////////////

#new-voices {
  @include honeycomb.create(4, 4, 7rem);

  .caption {
    grid-column: 5/8;
    grid-row: 11/14;
    align-self: center;
  }
}

$new-voices-pics-placement:
// pic  half-column  row
1  2  1 100%,
2  3  2,
3  2  3 40%,
4  1  4;

@include honeycomb.place-set($new-voices-pics-placement, "new-voices");


#new-voices2 {
  // Darken a little bit so it stands out from the page's white background
  filter: brightness(0.95);
}

/////////////////////////////////////////////////////////////

#wikimania2017 {
  @include honeycomb.create(4, 4, 7rem);

  .caption {
    grid-column: 1/4;
    grid-row: 11/14;
    align-self: center;
  }
}

$wikimania2017-pics-placement:
// pic  half-column  row
1  2  1 35%,
2  1  2,
3  3  2 70%,
4  2  3 60%,
5  3  4 5%;

@include honeycomb.place-set($wikimania2017-pics-placement, "wikimania2017-pic");


#wikimania2017-pic2 {
  // Darken a little bit so it stands out from the page's white background
  filter: brightness(0.95);
}

